<template>
  <div class="border border-primary border-bottom-0 rounded mb-2 ">
    <code class="badge badge-primary d-block text-left p-2 d-flex justify-content-between">
      <span v-b-tooltip.hover title="Translation key">
        <b-icon icon="caret-right" class="bg-white" variant="dark"></b-icon> {{translation.ident}}
      </span>
      <span class="badge badge-light badge-pill" v-b-tooltip.hover
        title="Amount of translations">{{translation.values.length}}</span>
    </code>
    <div v-for="(value, index) in translation.values" :key="index">
      <TranslationValue :translationValue="value" :defaultValue="getDefaultValue" />
    </div>
    <TranslationAdd :translation="translation" :extension="extension" />



  </div>
</template>

<script>
  import TranslationValue from './TranslationValue';
  import TranslationAdd from './TranslationAdd';
  export default ({
    name: 'Translation',
    props: ['translation','extension'],
    computed: {
      getDefaultValue: function () {
        for (let i = 0; i < this.translation.values.length; i++) {
          if (this.translation.values[i].ident == 'default') {
            return this.translation.values[i];
          }
        }
        return null;
      }
    },

    components: {
      TranslationValue,
      TranslationAdd
    }
  })
</script>