import axios from 'axios';
import * as utility from './../../scripts/Utility.js'

const state = {
    extensions: []
}

const mutations = {
    UPDATE_EXTENSIONS (state, payload) {
        console.log(payload);
        state.extensions = payload;
    },
    UPDATE_TRANSLATION_VALUE (state, payload) {
        state.extensions = payload;
    },

}

const actions = {
    getExtensions ({ commit }) {
        axios.get(utility.metaAttribute('api-extensions'))
            .then((response) => {
                console.log(response);
                
                if (response.status === "success") {
                    commit('UPDATE_EXTENSIONS', response.data.data);
                }

                
            })
            .catch(() => {
                console.log("DISPATCH");
                window.dispatchEvent(new Event('CRITICAL_ERROR'));
            })
    },
    addTranslationValue( context , payload) {
        axios.post('/api/translation/' + payload.extension + '/' + payload.translation)
            .then((response) => {
                console.log(response);
                for(let extension in this.state.extensions){
                    if(extension.uid === parseInt(payload.extension)){
                        context.commit('UPDATE_TRANSLATION_VALUE', response.data);
                    }
                }
               
            });
    },
}

const getters = {
    extensions: state => state.extensions,
}

const extensionModule = {
    state,
    mutations,
    actions,
    getters
}

export default extensionModule;