<template>
  <div>
    <div class="d-flex align-items-center mb-3">
      <b-progress class="w-100" :max="maxLoadingTime" height="1.5rem" v-show="loading">
        <b-progress-bar :value="loadingTime" :label="`${((loadingTime / maxLoadingTime) * 100).toFixed(2)}%`">
        </b-progress-bar>
      </b-progress>
    </div>

    <b-skeleton-wrapper :loading="loading">
      <template #loading>
        <b-card>
          <b-skeleton width="85%"></b-skeleton>
          <b-skeleton width="55%"></b-skeleton>
          <b-skeleton width="70%"></b-skeleton>
          <hr />
          <b-skeleton width="85%"></b-skeleton>
          <b-skeleton width="55%"></b-skeleton>
          <b-skeleton width="70%"></b-skeleton>
          <hr />
          <b-skeleton width="85%"></b-skeleton>
          <b-skeleton width="55%"></b-skeleton>
          <b-skeleton width="70%"></b-skeleton>
          <hr />
          <b-skeleton width="85%"></b-skeleton>
          <b-skeleton width="55%"></b-skeleton>
          <b-skeleton width="70%"></b-skeleton>
        </b-card>
      </template>
      <b-card class="mb-5">
        <b-card-header>
          <h2>Choose your extension</h2>
        </b-card-header>
        <div class="accordion" role="tablist">
          <b-card no-body class="mb-1" v-for="extension in extensions" v-bind:key="extension.uid">
            <Extension :extension="extension" />
          </b-card>
        </div>
      </b-card>


    </b-skeleton-wrapper>
  </div>
</template>

<script>
  import Extension from './Extension.vue';
  export default {
    components: {
      Extension
    },
    name: 'FileList',
    computed: {
      extensions() {
        return this.$store.getters.extensions;
      }
    },
    data() {
      return {
        loading: false,
        loadingTime: 0,
        maxLoadingTime: 1,
        extensionTableFields: ['name', 'filename', 'lastUpdate', 'showDetails'],
      }
    },
    watch: {
      loading(newValue, oldValue) {
        if (newValue !== oldValue) {
          this.clearLoadingTimeInterval()

          if (newValue) {
            this.$_loadingTimeInterval = setInterval(() => {
              this.loadingTime++
            }, 1000)
          }
        }
      },
      loadingTime(newValue, oldValue) {
        if (newValue !== oldValue) {
          if (newValue === this.maxLoadingTime) {
            this.loading = false
          }
        }
      }
    },
    created() {
      this.$store.dispatch('getExtensions');
      this.$_loadingTimeInterval = null
    },
    mounted() {
      this.startLoading()
    },
    methods: {
      rowClass(item, type) {
        if (!item || type !== 'row') return
        if (item.isTranslatable) return 'table-success'
      },
      clearLoadingTimeInterval() {
        clearInterval(this.$_loadingTimeInterval)
        this.$_loadingTimeInterval = null
      },
      startLoading() {
        this.loading = true
        this.loadingTime = 0
      }
    }
  }
</script>