<template>
  <div>
    <b-list-group>
      <b-list-group-item class="list-group-hover position-relative">

        <b-row>
          <b-col>
            <b-row>
              <b-col>

                <span class="hover-scale-svg" v-b-tooltip.hover title="Language Code">
                  <img :src="languageCodeInlineStyle" class="flag-svg rounded-circle" /> <span
                    class="text-uppercase p-2 font-weight-bold ">{{ translationValue.ident }}</span>

                  <b-badge pill v-if="changed" variant="warning" class="p-2 ml-2" v-b-tooltip.hover
                    title="Changes will be saved">
                    <b-icon icon="asterisk"></b-icon>
                  </b-badge>
                </span>
              </b-col>
            </b-row>
            <b-row>
              <b-col cols="6">
                <b-progress class="m-2" height="2rem" v-show="savingTime" :max="savingInSec" variant="primary">
                  <b-progress-bar :value="remainingSaveTime">
                    <span class="p-2">Saving in <strong>{{ remainingSaveTime }} seconds</strong></span>
                  </b-progress-bar>
                </b-progress>
              </b-col>
            </b-row>
          </b-col>
          <!-- Source -->
          <b-col v-if="!isDefault">
            <small class="font-weight-light">Source</small>
            <b-input-group size="sm" class="mb-2">
              <b-input-group-prepend is-text>
                <b-icon icon="clipboard"></b-icon>
              </b-input-group-prepend>
              <b-form-textarea disabled id="textarea-default" :value="defaultValue.value"
                placeholder="Enter something..." rows="3" max-rows="10">
              </b-form-textarea>
            </b-input-group>
          </b-col>

          <!-- Translation -->
          <b-col cols="1" v-if="!isDefault" align-self="center">
            <b-button v-if="!isAutoTranslating" size="sm" variant="primary" v-b-tooltip.hover
              title="Autotranslate from google" @click="translateGoogle">
              <b-icon icon="google"></b-icon>
              <b-icon icon="chevron-double-right" animation="cylon" class="ml-2"></b-icon>
            </b-button>
            <b-spinner v-if="isAutoTranslating" variant="primary" label="Getting data..."></b-spinner>
          </b-col>
          <b-col cols="4">
            <small class="font-weight-light">Translation</small>
            <b-input-group size="sm" class="mb-2">
              <b-input-group-prepend is-text>
                <b-icon icon="clipboard-data"></b-icon>
              </b-input-group-prepend>
              <b-form-textarea id="textarea" :value="ownValue" placeholder="Enter something..." rows="3" max-rows="10"
                :disabled="isAutoTranslating" v-bind:class="{'border-warning' : changed === true}"
                v-on:update="onChange">
              </b-form-textarea>
            </b-input-group>

          </b-col>
        </b-row>
        <b-dropdown variant="link" size="sm" class="m-2 position-absolute" style="top:-.5rem;right:-.5rem" v-b-tooltip.hover title="Further options" no-caret dropleft>
          <template #button-content>
            <b-icon icon="three-dots-vertical" ></b-icon>
          </template>
          <b-dropdown-item href="#">Delete</b-dropdown-item>
        </b-dropdown>

      </b-list-group-item>
    </b-list-group>
  </div>
</template>

<script>
  export default ({
    name: 'TranslationValue',
    props: ['translationValue', 'defaultValue'],
    data() {
      return {
        changed: false,
        isAutoTranslating: false,
        savingInSec: 10,
        savingTime: 0,
        isDefault: (this.translationValue.ident === 'default'),
        languageCodeInlineStyle: "/assets/svg/" + this.translationValue.ident.toUpperCase() + ".svg"
      }
    },
    computed: {
      remainingSaveTime: function () {
        return this.savingInSec - this.savingTime;
      },
      ownValue: function () {
        return this.translationValue.value;
      }
    },
    watch: {
      changed(newValue, oldValue) {
        if (newValue !== oldValue) {
          this.clearLoadingTimeInterval()
          this.savingTime = 0

          if (newValue) {
            this.$_loadingTimeInterval = setInterval(() => {
              this.savingTime++
            }, 1000)
          }
        }
      },
      savingTime(newValue, oldValue) {
        if (newValue !== oldValue) {
          if (newValue === this.savingInSec) {
            this.changed = false
          }
        }
      }
    },
    methods: {
      onChange: function (val) {
        this.translationValue.value = val;
        this.startSaving();
      },
      startSaving() {
        this.changed = true;
        this.savingTime = 0
      },
      clearLoadingTimeInterval() {
        clearInterval(this.$_loadingTimeInterval)
        this.$_loadingTimeInterval = null
      },
      translateGoogle() {
        this.isAutoTranslating = true;
      }
    },
    created() {
      this.$_loadingTimeInterval = null
    },

  })
</script>
<style>
  .flag-svg {
    background-size: cover;
    width: 1.5rem;
    height: 1.5rem;
  }

  .hover-scale-svg img {
    transition: .15s;
  }

  .hover-scale-svg:hover img {
    transform: scale(1.75);
  }

  .list-group-hover:hover {
    background: #f8f9fa;
  }

  textarea:disabled {
    cursor: not-allowed;
  }
</style>