<template>
  <div>

    <b-list-group-item class="list-group-hover">
      <b-row>
        <b-col cols="2">
          <b-input-group>
            <b-form-input type="search" v-model="languageToAdd" placeholder="Add Language"></b-form-input>

            <template #append>
              <b-button size="sm" variant="secondary" @click="showAddModal = !showAddModal">
                <b-icon icon="caret-down-fill"></b-icon>
              </b-button>
              <b-button size="sm" variant="success" :disabled="languageToAdd ? false : true" @click="addLanguage">
                <b-icon icon="clipboard-plus"></b-icon> Add
              </b-button>

            </template>
          </b-input-group>
          <b-modal size="xl" v-model="showAddModal" lazy>
            <b-card-group deck>
              <b-card header="Languages" header-tag="header" footer="Card Footer" footer-tag="footer"
                title="Select a language-code">
                <b-card-text>
                  <b-form-input v-model="languageFilter" placeholder="Filter"></b-form-input>
                </b-card-text>
                <b-row>
                  <b-col cols="1" v-for="(language, key) in languages" :key="key">
                    <b-form-checkbox v-model="selected"  button>
                      {{language.code}}
                    </b-form-checkbox>
                  </b-col>
                </b-row>


              </b-card>


            </b-card-group>

          </b-modal>
        </b-col>
      </b-row>
    </b-list-group-item>

  </div>
</template>

<script>
  export default ({
    name: 'TranslationAdd',
    props: ['translation', 'extension'],
    computed: {
      languages() {
        return this.$store.getters.getLanguages;
      },
      modalId() {
        return Math.floor(Math.random() * 99999);
      },
      errorPath() {
        return "/assets/svg/NA.svg"
      }
    },
    data() {
      return {
        getBody: document.getElementById('app'),
        showAddModal: false,
        selected: 'radio1',
        languageFilter: "",
        languageToAdd: "",
        fields: ['lang_code', 'last_name', 'age'],

      }

    },
    methods: {
      languageCode: function (ident) {
        return "/assets/svg/" + ident.toUpperCase() + ".svg"
      },
      addLanguage: function(){
        this.$store.dispatch('addTranslationValue', {extension:this.extension.uid,translation:this.translation.uid,lang:this.languageToAdd});
      }
    }

  })
</script>