<template>

  <b-card header-tag="header" footer-tag="footer">

    <Translations :translations="locallang.translations" :extension="extension" />

    <template #header>
      <div class="d-flex justify-content-between align-items-center">
        <span>
          <b-icon icon="info-circle-fill" variant="dark"></b-icon> <span class="text-muted">File:</span>
          <u>{{locallang.filename}}</u>
        </span>
        <b-button-group class="float-right" >
          <span class="border border-primary text-primary p-1 pr-3 rounded-left">Actions: </span>
          <b-button title="Overwrite File" variant="primary" size="sm" v-b-tooltip.hover>
            <b-icon icon="cloud-upload" aria-hidden="true"></b-icon>
          </b-button>
          <b-button title="Reimport from filesystem" variant="primary" size="sm" v-b-tooltip.hover>
            <b-icon icon="cloud-download" aria-hidden="true"></b-icon>
          </b-button>
        </b-button-group>

      </div>

    </template>
    <template #footer>
      <div class="row">
        <div class="col-4">
          <b-button variant="secondary" size="sm">
            <b-icon icon="arrow-repeat" aria-hidden="true"></b-icon> Reimport from filesystem
          </b-button>
        </div>
        <div class="col-4">

        </div>
        <div class="col-4">

        </div>
      </div>

    </template>
  </b-card>

</template>

<script>
  import Translations from '../translation/Translations';
  export default ({
    name: 'Locallang',
    props: ['locallang','extension'],
    data() {
      return {
        locallangTableFields: ['filename'],
      }
    },
    methods: {
      rowClass(item, type) {
        if (!item || type !== 'row') return
        if (item.isTranslatable) return 'table-success'
      },
    },
    components: {
      Translations
    }
  })
</script>