<template>
    <div>
        <div id="accordion-1"></div>
        <b-card-header header-tag="header" class="p-1" role="tab">
            <b-button block v-b-toggle.accordion-1 variant="primary" class="text-left">
                <b-icon icon="arrow-down-circle-fill" variant="light" :rotate="iconRotate"></b-icon> {{ extension.name }}
            </b-button>
        </b-card-header>
        <b-collapse id="accordion-1" role="tabpanel" v-model="collapsed">
            <Locallangs :locallangs="getLocallangs" :extension="extension" />
        </b-collapse>
        
    </div>
</template>

<script>
import Locallangs from './locallang/Locallangs';
export default({
    name: 'Extension',
    props: ['extension'],
    data() {
      return {
        locallangTableFields: ['filename'],
        collapsed:false,
      }
    },
    computed: {
        getLocallangs(){
            return this.extension.locallangs
        },
        iconRotate(){
            return (this.collapsed) ? 180 : 0;
        }
    },
    methods: {
        rowClass(item, type) {
        if (!item || type !== 'row') return
        if (item.isTranslatable) return 'table-success'
      },
    },
    components: {
        Locallangs
    }
})
</script>
